import React, { useContext, useState } from 'react'
import { ScrumboardContext } from './ScrumboardContext'
import { Draggable } from 'react-beautiful-dnd';
import BoardCard from './BoardCard';
import { modalModeTypes } from './utils';

const Board = ({ title, contents, index, isScrollable, isCombineEnabled, useClone, progress }) => {
	const { updateModal, updateModalMode, updateCurrentListId, updateCardData, } = useContext(ScrumboardContext)

	const onUpdateCardModal = (obj, listId) => {
		updateModal(true)
		updateModalMode(modalModeTypes(1))
		updateCurrentListId(listId)
		updateCardData(obj)
	}

	return (
		<Draggable draggableId={title} index={index}>
			{
				(provided, snapshot) => (
					<div className="board-column" ref={provided.innerRef} {...provided.draggableProps}>
						<div className="ahm-card-container ahm-card-container-green board-title" {...provided.dragHandleProps}>
							<h4 className="mb-0 ahm-card-container-heading" >{title}</h4>
						</div>
						<BoardCard
							listId={title}
							listType="CONTENT"
							className={snapshot.isDragging ? 'is-dragging' : ''}
							contents={contents}
							internalScroll={isScrollable}
							isCombineEnabled={isCombineEnabled}
							useClone={useClone}
							progress={progress}
						/>
					</div>
				)
			}
		</Draggable>
	)
}

export default Board
